<script setup>
	import {  computed,  reactive, toRefs, onMounted } from 'vue';
	import useVuelidate from '@vuelidate/core';
	import { required, numeric, } from '@/services/validators';
	import { useApp } from '@/composables/app.js';
	import { useEditPage } from '@/composables/editpage.js';
	const props = defineProps({
		id: [String, Number],
		pageName: {
			type: String,
			default: 'registro',
		},
		routeName: {
			type: String,
			default: 'registroedit',
		},
		pagePath: {
			type : String,
			default : 'registro/edit',
		},
		apiPath: {
			type: String,
			default: 'registro/edit',
		},
		submitButtonLabel: {
			type: String,
			default: "Actualizar",
		},
		formValidationError: {
			type: String,
			default: "El formulario no es válido",
		},
		formValidationMsg: {
			type: String,
			default: "Por favor complete el formulario",
		},
		msgAfterUpdate: {
			type: String,
			default: "Registro actualizado con éxito",
		},
		showHeader: {
			type: Boolean,
			default: true,
		},
		showSubmitButton: {
			type: Boolean,
			default: true,
		},
		redirect: {
			type : Boolean,
			default : true,
		},
		isSubPage: {
			type : Boolean,
			default : false,
		},
	});
	const app = useApp();
	const formDefaultValues = Object.assign({
		id_cabecera: "", 
		dimension: "", 
		subdimension: "", 
		componente: "", 
		item: "", 
		medicion: "", 
		base: "", 
		status: "", 
		valor: "", 
		porcentaje: "", 
		comen: "", 
		link_evidencias: "", 
		criterios: "", 
		orden_dim: "", 
		orden_subdim: "", 
		subcriterios: "", 
	}, props.pageData);
	const formData = reactive({ ...formDefaultValues });
	function onFormSubmited(response) {
		app.flashMsg(props.msgAfterUpdate);
		if(props.redirect) app.navigateTo(`/registro`);
	}
	const rules = computed(() => {
		return {
			id_cabecera: { required, numeric },
			dimension: {  },
			subdimension: {  },
			componente: {  },
			item: {  },
			medicion: { numeric },
			base: { numeric },
			status: { numeric },
			valor: { numeric },
			porcentaje: { numeric },
			comen: {  },
			link_evidencias: {  },
			criterios: { numeric },
			orden_dim: { numeric },
			orden_subdim: { numeric },
			subcriterios: {  }
		}
	});
	const v$ = useVuelidate(rules, formData); //form validation
	const page = useEditPage(props, formData, v$, onFormSubmited);
	//page state
	const {
		submitted, // form api submitted state - Boolean
		saving, // form api submiting state - Boolean
		loading, // form data loading state - Boolean
		pageReady, // if api data loaded successfully
	} = toRefs(page.state);
	//page computed propties
	const {
		apiUrl, // page current api path
		currentRecord, // current page record  - Object
	} = page.computedProps
	//page methods
	const { 
		load, // get editable form data from api
		submitForm, // submit form data to api
		getErrorClass, // return error class if field is invalid- args(fieldname)
		getFieldError, //  get validation error message - args(fieldname)
		isFieldValid, // check if field is validated - args(fieldname)
		 // map api datasource  to Select options label-value
	} = page.methods;
	onMounted(()=>{
		const pageTitle = "Editar";
		app.setPageTitle(props.routeName, pageTitle); // set browser page title
		load();
	});
</script>
